:root {
  --ff-body: "Barlow", sans-serif;
  --ff-headings: "Fraunces", serif;
}

.fw-600 {
  font-weight: 600;
}

.fw-700 {
  font-weight: 700;
}

.fw-900 {
  font-weight: 900;
}

:root {
  --white: hsl(0, 0%, 100%);
  --neutral-200: hsl(210, 4%, 67%);
  --neutral-400: hsl(232, 10%, 55%);
  --neutral-700: hsl(213, 9%, 39%);
  --neutral-900: hsl(212, 27%, 19%);
  --footer: hsl(168, 34%, 41%);
  --photography-txt: hsl(198, 62%, 26%);
  --graphic-txt: hsl(167, 40%, 24%);
  --yellow: hsl(51, 100%, 49%);
  --soft-red: hsl(7, 99%, 70%);
}

.text-100 {
  color: var(--white);
}

.text-200 {
  color: var(--neutral-200);
}

.text-400 {
  color: var(--neutral-400);
}

.text-700 {
  color: var(--neutral-700);
}

.text-900 {
  color: var(--neutral-900);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-body);
}

img {
  display: block;
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  font-weight: 600;
  font-size: 1.125rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--ff-headings);
}

a,
a:visited,
a:active {
  text-decoration: none;
  text-transform: capitalize;
}

.title {
  font-size: 3.4375rem;
  line-height: 0.9375em;
}

.graphic-design {
  color: var(--graphic-txt);
}

.photography {
  color: var(--photography-txt);
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 48em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex {
  display: flex;
}

.flex-jc-sb {
  justify-content: space-between;
}

.flex-ai-c {
  align-items: center;
}

header {
  height: 100vh;
  padding: 1.875em 1.25em;
  text-align: center;
  background-size: 100% 100%;
  background-position: center;
  background-image: url("../images/mobile/image-header.jpg");
}
@media (min-width: 48em) {
  header {
    background-image: url("../images/desktop/image-header.jpg");
  }
}

.hero {
  height: 100%;
  flex-direction: column;
}

.hamburger {
  cursor: pointer;
}
@media (min-width: 48em) {
  .hamburger {
    display: none;
  }
}

.logo svg {
  fill: var(--white);
}

.nav {
  position: relative;
}
.nav__list {
  padding: 1.25em;
  min-height: 18.75rem;
  background-color: var(--white);
  position: absolute;
  top: 5.3125rem;
  left: 0;
  right: 0;
  z-index: 5;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transform: scale(0);
  transform-origin: top right;
  transition: transform 200ms ease-out;
}
.nav__list.active {
  transform: scale(1);
}
.nav__list::before {
  content: "";
  position: absolute;
  right: 0;
  top: -30px;
  border: 30px solid transparent;
  border-right-color: var(--white);
}
@media (min-width: 48em) {
  .nav__list::before {
    display: none;
  }
}
@media (min-width: 48em) {
  .nav__list {
    padding: 0;
    min-height: auto;
    transform: scale(1);
    position: static;
    flex-direction: row;
    justify-content: flex-end;
    background-color: transparent;
  }
}
.nav__link {
  color: var(--neutral-400);
  border-radius: 50px;
  padding: 0.625em 1.25em;
  transition: 300ms ease-out;
}
.nav__link:hover, .nav__link:focus {
  color: var(--neutral-900);
}
.nav__link.cta {
  color: var(--neutral-900);
  background-color: var(--yellow);
  transition: 300ms ease-out;
}
.nav__link.cta:hover, .nav__link.cta:focus {
  opacity: 0.75;
}
@media (min-width: 48em) {
  .nav__link.cta {
    margin: 0 0.25em;
    background-color: var(--white);
  }
  .nav__link.cta:hover, .nav__link.cta:focus {
    color: var(--white);
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.5);
  }
}
@media (min-width: 48em) {
  .nav__link {
    color: var(--white);
  }
}

.about {
  text-align: center;
}
.about .card {
  overflow: hidden;
  flex-direction: column;
}
.about .card__content {
  max-width: 43.75rem;
  padding: 3.125em 1.25em;
}
@media (min-width: 48em) {
  .about .card__content {
    padding: 0 3.125em;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}
.about .card__title {
  margin: 0;
  font-size: 2.25rem;
}
@media (min-width: 48em) {
  .about .card__title {
    align-self: flex-start;
  }
}
.about .card__link {
  position: relative;
  font-family: var(--ff-headings);
  text-transform: uppercase;
}
.about .card__link.transform::after {
  background-color: var(--soft-red);
}
.about .card__link::after {
  content: "";
  position: absolute;
  width: 90%;
  height: 7px;
  border-radius: 3px;
  bottom: -1px;
  left: 5%;
  z-index: -5;
  background-color: var(--yellow);
  transition: 300ms ease-out;
  opacity: 0.5;
}
.about .card__link:hover::after, .about .card__link:focus::after {
  opacity: 1;
}
@media (min-width: 48em) {
  .about .card__link {
    align-self: flex-start;
  }
}
.about .card__img {
  order: -1;
  min-width: 23.4375rem;
  min-height: 15.3125rem;
}
.about .card__img img {
  width: 100%;
}
@media (min-width: 48em) {
  .about .card__img-first {
    order: 2;
  }
}
@media (min-width: 48em) {
  .about .card {
    align-items: stretch;
    flex-direction: row;
  }
  .about .card > * {
    flex-basis: 50%;
  }
}
@media (min-width: 48em) {
  .about {
    text-align: left;
  }
}

.services {
  text-align: center;
  flex-direction: column;
}
@media (min-width: 48em) {
  .services {
    flex-direction: row;
  }
}
.services .card {
  padding: 19.375em 1.25em 2.5em;
  min-height: 29.375rem;
  max-height: 36.875rem;
  background-size: cover;
}
@media (min-width: 48em) {
  .services .card {
    flex-basis: 50%;
    background-position: center;
    padding: 20em 4.6875em 2.5em;
  }
}

.photography {
  background-image: url("../images/mobile/image-photography.jpg");
}
@media (min-width: 48em) {
  .photography {
    background-image: url("../images/desktop/image-photography.jpg");
  }
}

.graphic-design {
  background-image: url("../images/mobile/image-graphic-design.jpg");
}
@media (min-width: 48em) {
  .graphic-design {
    background-image: url("../images/desktop/image-graphic-design.jpg");
  }
}

.testimonials {
  text-align: center;
  padding: 2.8125em 1.25em;
}
.testimonials .container {
  flex-direction: column;
}
@media (min-width: 48em) {
  .testimonials .container {
    flex-direction: row;
  }
}
.testimonials .card {
  max-width: 375px;
  margin: 0.9375em auto 0;
  padding: 1.25em 1.25em 0;
}
.testimonials .card__title {
  letter-spacing: 2px;
  text-transform: uppercase;
}
.testimonials .card__text {
  font-size: 1rem;
}
.testimonials .card__img {
  width: 2.8125rem;
  height: 2.8125rem;
  margin: 0 auto 1.625rem;
  border-radius: 50%;
}
@media (min-width: 48em) {
  .testimonials {
    padding: 5em 2.8125em;
  }
}

.member {
  margin: 0.5em 0;
}

.company-role {
  margin: 0;
  font-size: 0.9375rem;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
}

.footer {
  min-height: 16.875rem;
  background-color: var(--footer);
  justify-content: center;
  flex-direction: column;
}
.footer__logo svg {
  margin: 2.1875em 0;
  fill: var(--graphic-txt);
}
.footer__links {
  margin-bottom: 3.75em;
  justify-content: center;
}
.footer__link {
  margin: 0 1.25em;
  color: var(--graphic-txt);
  text-transform: capitalize;
  transition: 300ms ease-out;
}
.footer__link:hover, .footer__link:focus {
  color: var(--white);
}
.footer__social-links {
  justify-content: center;
}
.footer .social__link svg {
  margin: 0 1.25em 3.75em;
  fill: var(--graphic-txt);
  transition: 300ms ease-out;
}
.footer .social__link svg:hover, .footer .social__link svg:focus {
  fill: var(--white);
}
.footer .attribution {
  font-size: 11px;
  text-align: center;
  margin-bottom: 1em;
}
.footer .attribution a {
  color: #3e52a3;
}